.ux-parallax-canvas {
  position: relative;
  overflow: hidden;
  display: block;
  padding: 0;
  margin: 0;
  height: 600px;
  height: 100vh;
}

.ux-parallax-animated {
  transform-style: preserve-3d;
}

.ux-parallax-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
}

.ux-parallax-wrapper.ux-parallax-horizontal {
  height: 100%;
  width: auto;
}

.ux-parallax-background {
  will-change: transform;
}

.ux-parallax-auto {
  height: auto;
}

.ux-parallax-auto .ux-parallax-content {
  position: relative;
  display: block;
  height: auto;
}

.ux-parallax-auto .ux-parallax-content .ux-parallax-content-container {
  position: relative;
  display: block;
}

.ux-parallax-content {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: table;
  z-index: 99;
}

.ux-parallax-content.ux-parallax-content-center > .ux-parallax-content-container {
  display: table-cell;
  vertical-align: middle;
}

.ux-parallax-content.ux-parallax-content-top > .ux-parallax-content-container {
  display: table-cell;
  vertical-align: top;
}

.ux-parallax-content.ux-parallax-content-bottom > .ux-parallax-content-container {
  display: table-cell;
  vertical-align: bottom;
}

.ux-parallax-canvas {
  display: block;
  position: relative;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.ux-parallax-layer {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
}

.ux-parallax-layer:nth-child(1) {
  z-index: 9;
}

.ux-parallax-layer:nth-child(2) {
  z-index: 8;
}

.ux-parallax-layer:nth-child(3) {
  z-index: 7;
}

.ux-parallax-layer:nth-child(4) {
  z-index: 6;
}

.ux-parallax-layer:nth-child(5) {
  z-index: 5;
}

.ux-parallax-layer:nth-child(6) {
  z-index: 4;
}

.ux-parallax-layer:nth-child(7) {
  z-index: 3;
}

.ux-parallax-layer:nth-child(8) {
  z-index: 2;
}

.ux-parallax-layer:nth-child(9) {
  z-index: 1;
}

.ux-parallax-background,
.ux-parallax-background img {
  position: relative;
  width: 100%;
  height: auto;
  max-width: none;
}

.ux-parallax-background,
.ux-parallax-background-inside {
  display: none;
}

.ux-parallax-built .ux-parallax-background, .ux-parallax-built
.ux-parallax-background-inside {
  display: block;
}

.ux-parallax {
  visibility: hidden;
  will-change: transform;
}

.ux-parallax.ux-parallax-built {
  visibility: visible;
}

@keyframes loader {
  0% {
    transform: rotateZ(0deg) translateZ(0);
  }
  25% {
    transform: rotateZ(180deg) translateZ(0);
  }
  50% {
    transform: rotateZ(180deg) translateZ(0);
  }
  75% {
    transform: rotateZ(360deg) translateZ(0);
  }
  100% {
    transform: rotateZ(360deg) translateZ(0);
  }
}

@keyframes loader-inner {
  0% {
    height: 0%;
  }
  25% {
    height: 0%;
  }
  50% {
    height: 100%;
  }
  75% {
    height: 100%;
  }
  100% {
    height: 0%;
  }
}

.ux-parallax-loader {
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  border: 4px solid #fff;
  top: 50%;
  left: 50%;
  border-radius: 3px;
  margin-left: -25px;
  margin-top: -25px;
  animation: loader 2s infinite ease;
  z-index: 99;
}

.ux-parallax-loader-inner {
  vertical-align: top;
  display: inline-block;
  width: 100%;
  background-color: #fff;
  animation: loader-inner 2s infinite ease-in;
}
